@mixin reset-margins {
    margin: 0;
    padding: 0;
}

@mixin reset-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin reset-flex {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
    align-content: space-between;
}

@mixin reset-flex-horizontal {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
}

@mixin reset-flex-stretch {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: stretch;
    align-content: stretch;
}

@mixin reset-button {
    padding: 0.8em 1.4em;
    font-size: 2rem;
    font-weight: 400;
    line-height: 1;
    letter-spacing: 0;
    // text-transform: uppercase;
    background: none;
    border: none;
    border-radius: 0;
    transition: all 0.3s ease-in-out;
}

@mixin button-style($color, $background-color, $border-color) {
    color: $color;
    background-color: $background-color;
    border: 2px solid $border-color;
}

@mixin button-hover-style($color, $background-color, $border-color) {
    color: $color;
    background-color: $background-color;
    border: 2px solid $border-color;
}

@mixin button-arrow {
    position: relative;
    padding-right: 6em !important;
    // font-weight: 600;
    text-align: left;
    text-transform: uppercase;

    // background-color: $complementary-color;
    // border-color: $complementary-color;

    // transition: all 0.2s;

    &:after {
        position: absolute;
        top: 10px;
        right: 20px;
        content: '\f3d3';
        font-family: 'Ionicons';
        font-size: 32px;
        font-weight: normal;
    }
}

@mixin button-hover {
    color: $white;
    background-color: $complementary-color;
    border-color: $complementary-color;
}


@mixin button-hover-shadow {
    color: $white !important;
    background-color: $complementary-color !important;
    border-color: $white !important;

    // width: 80%;
    // padding-right: 8em;

    box-shadow: -6px 6px 0 $main-color;
    // box-shadow: 0 0 8px $complementary-color;
}


@mixin button-hover-shadow-white {
    color: $white !important;
    background-color: $complementary-color !important;
    border-color: $complementary-color !important;

    // width: 80%;
    // padding-right: 8em;

    // box-shadow: -6px 6px 0 $white;

    box-shadow: -6px 6px 0 $white,
                0 19px 38px rgba(0, 0, 0, 0.30),
                0 15px 12px rgba(0, 0, 0, 0.22);


    // box-shadow: 0 0 8px $complementary-color;
}

@mixin text-link($color,$hover,$shadow-color,$shadow-hover) {
    color: $color;
    font-weight: 600;
    box-shadow: 0 1px 0 $shadow-color;
    transition: all 0.2s linear;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $complementary-color;
        box-shadow: 0 1px 0 $shadow-hover;
    }
}

@mixin link {
    color: $complementary-color;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $black;
    }
}

@mixin link-hover {
    text-decoration: none;
    color: $complementary-color;
    // box-shadow: 0 2px 0 $black;
}


@mixin link-line($color) {
    color: $color;
    box-shadow: 0 2px 0 transparent;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $color;
        box-shadow: 0 2px 0 $color;
    }
}

@mixin link-line-black {
    color: $black;
    box-shadow: 0 3px 0 transparent;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $black;
        background-color: transparent;
        box-shadow: 0 3px 0 $black;
    }
}

@mixin link-line-black-smaller {
    color: $black;
    box-shadow: 0 2px 0 transparent;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $black;
        background-color: transparent;
        box-shadow: 0 2px 0 $black;
    }
}

@mixin link-line-color {
    color: $complementary-color;
    box-shadow: 0 1px 0 transparent;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $black;
        box-shadow: 0 1px 0 $black;
    }
}

@mixin link-bg-color {
    color: $black;

    &:hover,&:focus,&:active {
        text-decoration: none;
        // color: $black;
        background-color: rgba( $complementary-color, 0.3 );
        // background-color: lighten( $complementary-color, 10% );
        // background-color: desaturate( $complementary-color, 20% );
    }
}


