//     box-shadow: 0 2px 0 $black;
// }


@mixin br-to-space {
    content:' ';

    &:after {
        content:' ';
    }
}



@mixin icons-ionicons($top,$right,$bottom,$left) {
    position: absolute;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
    font-family: 'Ionicons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    font-feature-settings: 'liga';
    transition: transform 0.3s ease-in-out;
}

@mixin reset-figure {
    position: relative;
    margin: 0 0;
    overflow: hidden;
}

@mixin stretch-to-left {
    @include reset-figure;
    margin-left: -22vw;

    img {
        width: 100%;
        max-width: 100%;
        height: auto;
    }
}

@mixin stretch-to-right {
    @include reset-figure;
    margin-right: -22vw;

    img {
        width: 100%;
        max-width: 100%;
        height: auto;
    }
}

@mixin marked-image {
    position: relative;

    &:after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 4px;
        height: 50px;
        background-color: $black;
    }
}

@mixin marked-box {
    position: relative;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 4px;
        height: 40px;
        background-color: $black;
    }
}

@mixin flex-boxes {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-content: stretch;
    align-items: stretch;
    margin: 0 -1em;
}

@mixin card-boxes {
    position: relative;
    width: calc(50% - 2em);
    margin: 1em;
    text-align: left;
    color: inherit;
    border-radius: 0;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    transition: 0.3s;

    &:hover,&:focus,&:active {
        box-shadow: 0 15px 30px rgba(0,0,0,.2);
        // box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
    }

    &:after {
        content: "";
        width: 100%;
        height: 100%;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        outline: transparent solid 1px;
        box-shadow: 0 0 0 rgba(0, 0, 0, 0.15);
        transition: 0.3s;
    }

    &:hover:after {
        box-shadow: 0 15px 30px rgba(0, 0, 0, 0.17)
    }
}

@mixin box-hover {
    color: $white;
    background-color: $complementary-color;
    transition: 0.2s ease-out;
}

@mixin box-hover-bw {
    color: $white;
    background-color: $black;
    transition: 0.2s ease-in-out;
}

@mixin box-hover-shadow {
    background-color: $light-black;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    transition: 0.3s;

    &:hover,&:focus,&:active {
        box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
    }
}

@mixin box-hover-outline {
    outline: 4px solid transparent;
    // outline-offset: 4px;
    transition: 0.3s;

    &:hover,&:focus,&:active {
        outline: 4px solid $complementary-color;
    }
}

@mixin highlight-text {
    margin-bottom: 0;
    font-size: 4.5rem;
    font-weight: 300;
    line-height: 1.2;
}

@mixin smaller-text {
    font-size: 90%;
    font-weight: 300;
    line-height: 1.2;
}

@mixin img-hover-fx {
    position: relative;
    overflow: hidden;
    transition: 0.3s;

    img {
        width: 100%;
        transform-origin: center center 0;
        transition: 0.6s;
    }

    &:hover img {
        transform: scale(1.1) translate(0);
    }
}


@mixin keyframes($name) {
    @keyframes #{$name} {
        @content
    }
}

@mixin animation($value) {
    animation: $value;
}
