// CUSTOM SELECT
// --------------------------------------------------------------------------

// https://codepen.io/afalchi82/pen/aNXjgb
// https://codepen.io/vulchivijay/pen/rxyBpg


// https://custom-select.github.io/custom-select/
// https://github.com/custom-select/custom-select

// https://speckyboy.com/open-source-css-javascript-select-box-snippets/



// https://codepen.io/wallaceerick/pen/ctsCz

$select-color: $gray-medium;
$select-background: transparent;
$select-width: 28%;
$select-height: 42px;

.select-hidden {
    display: none;
    visibility: hidden;
}

.select {
    cursor: pointer;
    display: inline-block;
    position: relative;
    margin: 0 10px;
    font-size: 16px;
    color: $select-color;
    width: $select-width;
    height: $select-height;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        right: -10px;
        width: 2px;
        height: 100%;
        background-color: $white-dark;
    }

    &:last-child {
        &:before {
            display: none;
        }
    }

    select {
        + .select-styled {
            &:before {
                @include icons-ionicons(8px,auto,auto,10px);
                font-size: 24px;
                transition: all 0.2s;
            }
        }
    }

    #area {
        + .select-styled {
            &:before {
                content: "\f3ed";
            }
        }
    }

    #desk {
        + .select-styled {
            &:before {
                content: "\f229"; /*\f229*/ /*\f318*/ /*\f44f*/ /*\f4d2*/ /*\f2a5*/ /*\f229*/ /*\f228*/
            }
        }
    }

    #office {
        + .select-styled {
            &:before {
                content: "\f2a7"; /*\f2a7*/
            }
        }
    }
}

.select-styled {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 12px 10px 12px 40px;
    line-height: 1;
    text-align: left;
    background-color: $select-background;
    transition: all 0.2s ease-in;

    &:after {
        @include icons-ionicons(12px,22px,auto,auto);
        position: absolute;
        content: "\f3d0";
        font-size: 18px;
        color: $complementary-color;
        transition: all 0.2s;
    }

    &:hover {
        color: $black;
    }

    &:active, &.active {
        color: $black;
        // background-color: rgba(255,90,96,.1);
        box-shadow: 0 1px 0 $complementary-color;

        &:after {
            transform: rotate(180deg);
        }
    }
}

.select-options {
    display: none;
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    max-height: 295px;
    overflow-y: scroll;
    z-index: 999;
    margin: 1px 0 0 0;
    padding: 0;
    list-style: none;
    background-color: $white;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);

    li {
        margin: 0;
        padding: 10px 42px 10px 42px;
        line-height: 1;
        text-align: left;
        border-bottom: 1px solid $white-dark;
        transition: all 0.15s ease-in;

        &:last-child {
            border-bottom: none;
        }

        &:hover {
            color: $white;
            background-color: $complementary-color;
        }

        &[rel="hide"] {
            display: none;
        }

    }

}


