//
// TYPOGRAPHY
// --------------------------------------------------------------------------

// * Redefine the rem value while keeping it proportional.
// * Useful values, with default font-size of 16px:
// * • 62.5% -> 1rem = 10px, 0.1rem  = 1px
// * • 125%  -> 1rem = 20px, 0.05rem = 1px

html {
    font-size: 62.5%;
}

body {
    box-sizing: border-box;
    color: $black;
    font-family: $body-font;
    font-size: 1.8rem;
    font-weight: 400;
    // overflow-x: hidden;
    // letter-spacing: -.015em;

    // Support for all WebKit browsers.
    -webkit-font-smoothing: antialiased;
    // Support for Safari and Chrome.
    text-rendering: optimizeLegibility;
    // Support for Firefox.
    -moz-osx-font-smoothing: grayscale;
    // Support for IE.
    font-feature-settings: 'liga';
}


 // * With default user settings:
 // * • 0.625rem = 10px
 // * • 1.25rem  = 20px
 // * • 2.5rem   = 40px

// h1 { font-size: 1.25rem; }

// @media (min-width: 320px) {
//   h1 { font-size: calc( 3.125vw + 0.625rem ); }
// }

// @media (min-width: 960px) {
//   h1 { font-size: 2.5rem; }
// }

h1,h2,h3,h4,h5,h6,
.h1,.h2,.h3,.h4,.h5,.h6,.title {
    font-family: $header-font;
    font-weight: 600;
    letter-spacing: -.0415625em;
    line-height: 1.25;
    margin: 0 0;
    // margin: 3.5rem 0 2rem
}

.section-header {
    font-size: 4.2rem;
    font-weight: 700;
    line-height: 1.138888889;
    letter-spacing: 0;
    color: $main-color;
}

h1,.h1 {
    font-size: 3.6rem;
    line-height: 1.4;
}

h2,.h2 {
    font-size: 2.4rem;
    // letter-spacing: 0;
}

h3,.h3 {
    font-size: 1.8rem;
}

h4,.h4 {
    font-size: 2.4rem
}

h5,.h5 {
    font-size: 2.1rem
}

h6,.h6 {
    font-size: 1.6rem;
    letter-spacing: 0.03125em;
    text-transform: uppercase
}

p {
    line-height: 1.45;
    margin: 0 0 1.4em;

    strong {
        font-weight: 600
    }
}

.emphasis {
    @include highlight-text;
}

a {
    transition: all 0.3s ease-in-out;

    &:hover, &:focus, &:active {
        text-decoration: none;
        outline: none
    }
}

button {
    &:hover, &:focus, &:active {
        outline: none
    }
}

