//
// LAYOUT
// --------------------------------------------------------------------------

.main-nav-wrapper {
    position: absolute;
    z-index: 1;
    display: block;
    width: 100%;
    // background-color: white;

    nav {
        text-transform: uppercase;
    }
    .nav-link {
        font-weight: 400;
    }

    .navbar {
        padding: 0 1rem;
    }
}

#top-nav {
    position: absolute;
    top: 0;
    right: 0;

    .nav-link {
        margin: 0 0.5em;
        font-size: 80%;
        font-weight: 500;
        color: $black;

        &:hover, &:focus, &:active {
            @include link-hover;
            color: $black;
        }
    }
}

#main-nav {
    // border-bottom: 1px solid $white;
    margin-top: 2rem;

    .navbar-brand {
        position: relative;
        z-index: 1;
        font-size: 3rem;
        font-weight: 600;
    }

    .navbar-nav {
        position: relative;
        top: 20px;
        margin: 0 0 0 30px;
        // background-color: $white;

        > li {
            z-index: 1;
        }

        &.main-nav-left {
            &:before {
                content: '';
                position: absolute;
                bottom: -20px;
                right: -24px;
                width: 200%;
                height: 500%;
                background-color: $white;
            }

            .navbar-toggler {
                display: block;
                padding: .25rem 3rem .25rem 6rem;
                border: none;

                i {
                  font-size: 32px;
                  font-weight: normal;
                  color: $complementary-color;
                }
            }
        }
    }

    .nav-link {
        margin: 0 1em;
        font-weight: 600;
        text-transform: none;
        color: $main-color;

        &:hover, &:focus, &:active {
            @include link-hover;
        }
    }
}

footer {
    padding-bottom: 2em;
    background-color: $white-medium;

    .chat-button-wrapper {
        position: relative;
    }

    .chat-button {
        position: absolute;
        top: -30px;
        right: -36px;

        a {
            position: relative;
            display: block;
            width: 70px;
            height: 70px;
            text-align: center;
            color: $white;
            background-color: $complementary-color;
            border-radius: 50%;
            box-shadow: 0 0 0 0 $white;
            transition: all 0.2s ease-out;

            &:hover, &:focus, &:active {
                @include button-hover;
                color: $complementary-color;
                background-color: $white;
                // box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
                // box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
                box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
                transform: scale(1.1);

                &:after {
                    color: $complementary-color;
                }
            }

            &:after {
                position: absolute;
                top: 10px;
                left: 18px;
                content: '\e0b7';
                font-family: 'Material Icons';
                font-size: 36px;
            }
        }
    }

    img {
        margin-bottom: 0.8em;
    }
    p {
        font-size: 80%;
        color: $black;
    }

    .footer-info {
        padding: 1.6em 0;

        p {
            line-height: 1.2;
            color: $black;
        }
    }

    .footer-links {
        padding: 0.8em 0;
        border-top: 1px solid $gray-light;

        p {
            margin-bottom: 0;
            color: $gray-medium;
        }
    }

    .legal-info {
        @include reset-list;

        li {
            position: relative;
            margin-right: 0.4em;
            padding-right: 0.6em;

            &:after {
                position: absolute;
                top: 8px;
                right: 0;
                width: 1px;
                height: 15px;
                content: "";
                background: $white;
            }

            &:last-child {
                &:after {
                    display: none;
                }
            }
        }

        a {
            font-size: 90%;
            font-weight: 600;
            color: $gray-medium;

            &:hover, &:focus, &:active {
                @include link-hover;
            }
        }
    }

    .social-media {
        @include reset-list;

        li {
            margin-left: 0.2em;
        }

        a {
            display: block;
            width: 38px;
            height: 38px;
            text-align: center;
            color: $gray-medium;
            border-radius: 50%;
            transition: all 0.2s ease-in-out;

            &:hover, &:focus, &:active {
                @include button-hover;
            }

            i {
                line-height: 2;
            }
        }
    }

}


//
// BUTTONS
// --------------------------------------------------------------------------

.btn-solid {
    @include reset-button;
    color: $white;
    background-color: $medium-black;
    border-color: $medium-black;
}

.btn-main {
    @include reset-button;
    color: $white;
    background-color: $complementary-color;
    border: 1px solid transparent;
    border-color: $complementary-color;
    // font-weight: 600;
    text-transform: uppercase;
    white-space: nowrap;

    &.trans {
        text-transform: none;
        color: $complementary-color;
        background-color: transparent;
        border: 2px solid transparent;
        border-color: $complementary-color;

        &:hover, &:focus, &:active {
            // @include button-hover-shadow;
            color: $white !important;
            background-color: $complementary-color !important;
            border-color: $complementary-color !important;
            // box-shadow: -6px 6px 0 $main-color;
            box-shadow: -6px 6px 0 $main-color,
                0 19px 38px rgba(0, 0, 0, 0.30),
                0 15px 12px rgba(0, 0, 0, 0.22);
        }
    }

    &:hover, &:focus, &:active {
        // @include button-hover-shadow;
    }

    &.hover-white {
        &:hover, &:focus, &:active {
            @include button-hover-shadow-white;
        }
    }
}

.btn-border {
    @include reset-button;
    color: $black;
    background-color: $white;
    border: 1px solid $black;
    border-radius: 0.25rem;

    &.white {
        color: $white;
        background-color: transparent;
        border-color: $white;
    }

    &:hover, &:focus, &:active {
        @include button-hover;
        // @include button-hover-shadow;
    }

    &.view-more {
        min-width: 33.3333%;
    }
}

.btn-arrow {
    @include button-arrow;

    &:hover, &:focus, &:active {
        // @include button-hover;
        @include button-hover-shadow;
    }
}

.btn-border-arrow {
    @include reset-button;
    @include button-style($white, $complementary-color, $complementary-color);

    &:hover, &:focus, &:active {
        @include button-hover-style($complementary-color, $white, $white)
    }
}


.btn-border-white {
    @include reset-button;
    @include button-style($white, transparente, $white);
    @include button-arrow;

    &:hover, &:focus, &:active {
        @include button-hover-style($complementary-color, $white, $white);
    }
}


.btn-naked {
    @include reset-button;
    color: $black;
    background-color: $white;

    &:hover, &:focus, &:active {
        @include button-hover;
    }

    &.view-more {
        min-width: 33.3333%;
    }
}

.button-wrapper {
    text-align: center;
}


.button-bookmark {
    @include reset-button;
    position: absolute;
    top: 0;
    right: 0;
    width: 38px;
    height: 38px;
    margin: 0 0;
    padding: 0.2em;
    color: $complementary-color;
    background-color: transparent;
    border-color: transparent;
    border-radius: 50%;
    z-index: 3;

    &:after {
        position: relative;
        top: 0;
        left: 0;
        content: '\e867';
        font-family: 'Material Icons';
        font-size: 24px;
        font-weight: normal;
        line-height: 1.2;
    }

    &:hover, &:focus, &:active {
        // @include button-hover;
        color: $white;
        background-color: $complementary-color;
        border-color: $complementary-color;
    }

    &.bookmark-on {
        &:after {
            content: '\e866';
        }
    }
}



//
// HOME
// --------------------------------------------------------------------------

#home-header {
    $header-height: 100vh;
    $header-top: 114px;

    width: 100%;
    height: $header-height;
    margin-bottom: 5em;
    overflow: hidden;

    // border: 1px solid red;

    .full-width {
        display: block;
        width: 100vw;
        height: 100%;
        position: relative;
        left: 50%;
        right: 50%;
        margin: 0 -50vw;
        padding: 0 0;
        overflow: hidden;
        // border: 1px solid red;

        img {
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            max-width: none;
            height: auto;
        }
    }

    .header-wrapper {
        @include reset-flex;

        position: absolute;
        top: $header-top;
        width: 42vw;
        height: calc(#{$header-height} - #{$header-top});
        margin: 0 0;
        padding: 0 0;
        background-color: $white;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: -200%;
            width: 200%;
            height: 100%;
            background-color: $white;
        }

        .category {
            margin-bottom: 0.6em;
            font-size: 90%;
            font-weight: 600;
            text-transform: uppercase;
        }

        .header-info {
            padding: 3em 3em 0 0;
            background-color: $white;

            .category {
                color: $gray-medium;
            }

            h1 {
                margin-bottom: 1em;
                font-weight: 700;
                letter-spacing: -.03em;
            }
        }

        .header-button {
            $transition-hover: all 0.3s ease-out;

            position: relative;
            padding: 2em 4em 2.6em 0;
            background-color: $complementary-color;

            &:before {
                content: "";
                position: absolute;
                top: 0;
                left: -200%;
                width: 200%;
                height: 100%;
                background-color: $complementary-color;
            }

            &:after {
                content: "";
                position: absolute;
                top: 0;
                right: -35%;
                width: 35%;
                height: 100%;
                background-color: $main-color;
                transition: $transition-hover;
            }

            .category {
                color: $main-color;
            }

            .intro {
                font-weight: 600;
                line-height: 1.2;
                color: $white;
            }

            .icon {
                position: absolute;
                top: calc(50% - 32px);
                right: -20%;
                width: auto;
                height: auto;
                font-size: 64px;
                line-height: 1;
                color: $complementary-color;
                z-index: 3;
                transition: $transition-hover;
            }

            &:hover,&:focus,&:active {

                .icon {
                    right: -40%;
                    color: $main-color;
                }

                &:after {
                    right: -50%;
                    width: 50%;
                    background-color: $complementary-color;
                }
            }
        }
    }
}


