        line-height: 1;
    }

    .info-type {
        position: relative;
        margin-left: 20px;
        line-height: 1;

        &:before {
            position: absolute;
            top: -2px;
            left: -15px;
            content: '·';
            font-size: 1.4em;
            font-weight: 400;
            line-height: 1;
            color: $gray-dark;
        }
    }

    .info-author {
        display: block;
        margin-top: 0.2em;
        line-height: 1;

        a {
            @include link-line-color;
        }
    }

    .button-bookmark {
        position: absolute;
        top: 10px;
        right: 10px;
    }
}

.box-body {
    margin: 1.8em 0;

