        }
    }

    .info-time {
        margin-top: 0.5em;
        font-size: 110%;
        font-weight: 300;
        letter-spacing: 0;
        color: $medium-black;
    }
}

.box-footer {
    margin-top: auto;

    .list-inline-dots {
        li {
            font-size: 80%;
            line-height: 1;
        }
    }
}


// Box types

.box {
    @include reset-flex;
    justify-content: stretch;
    align-items: stretch;
    align-content: stretch;
    height: 100%;

    position: relative;
    min-height: 320px;
    margin: 0 0;
    padding: 2em;
    // background-color: $white;
    box-shadow: 0 3px 12px rgba(0, 0, 0, 0.16), 0 3px 12px rgba(0, 0, 0, 0.23);
    // box-shadow: 0 3px 6px rgba(255,255,255,0.16), 0 3px 6px rgba(255,255,255,0.23);
    // box-shadow: 0 10px 20px rgba(255,255,255,0.19), 0 6px 6px rgba(255,255,255,0.23);

    > div {
        display: inline-block;
        font-weight: 400;
        letter-spacing: 0;
        color: $medium-black;
        z-index: 1;
    }

    .info-date {
        font-size: 80%;
        // font-weight: 600;
    }

    .info-type {
        font-size: 90%;
    }

    &:before {
        // display: none;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        // border: 2px solid transparent;
        // outline-offset: 2px;

        background-color: $white;
        // background-color: $white-dark;
        box-shadow: 0 3px 12px rgba(255, 255, 255, 0.16), 0 3px 12px rgba(255, 255, 255, 0.23);
        transition: all 0.2s ease-out;
    }

    &.box-article {
        &:before {
            // background-color: $white;
        }
    }

    &.box-event {
        &:before {
            // background-color: $white-medium;
        }
    }

    &:hover,&:focus,&:active {

        .box-body {
            h3 {
                a {
                    background-color: rgba($complementary-color, 0.2);
                    // box-shadow: 0 1px 0 rgba($complementary-color, 0.3);

                    &:hover,&:focus,&:active {
                        background-color: transparent;
                        // box-shadow: 0 2px 0 $black;
                    }
                }
            }
        }

        &:before {
            background-color: $white;
            box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
            transform: scale(1.1);
        }
    }
}


// Box wrappers

.box-articles-wrapper {
    @include reset-flex-stretch;

    > .box {
        width: 31.5%;
    }
}

.box-news-wrapper {
    @include reset-flex-horizontal;
    align-items: center;
    position: relative;

    > * {
        z-index: 1;
    }

    &:after {
        content: '';
        position: absolute;
        top: -20%;
        right: 35%;
        width: 200%;
        height: 140%;
        background-color: $white-dark;
    }

    .box-news {
        flex: 40%;

        @include reset-flex;
        height: 100%;
        margin-right: 4em;
        justify-content: stretch;
        align-items: stretch;
        align-content: flex-start;

        .box-header {

            .info-type,
            .tag-link {
                font-weight: 700;
            }

            .info-author {
                font-size: 90%;
            }

            h2 {
                font-size: 4.4rem;
                font-weight: 700;
                line-height: 1.2;
                color: $main-color;
            }
        }

        .box-body {
            margin: 1.8em 0;

            h3 {

                a {
                    @include link-line-black;
                    font-size: 3.2rem;
                    font-weight: 700;
                    line-height: 1.3;
                    color: $gray-dark;
                }
            }

            .info-time {
                margin-top: 0.5em;
                font-size: 110%;
                font-weight: 300;
                letter-spacing: 0;
                color: $medium-black;
            }
        }

        .box-footer {
            .list-inline-dots {
                li {
                    font-size: 90%;
                }
            }
        }

    }

    .box-img {

        flex: 60%;
        @include reset-figure;

            box-shadow: 2px 2px 20px rgba(0,0,0,.25);

        img {
            // display: none;
            width: 100%;
            height: auto;
            max-height: 400px;
            object-fit: cover;
