//
// ACCORDION
// --------------------------------------------------------------------------

.accordion {
    margin: 3em 0 2em;

    &.panel-group {

        .panel,
        .panel-heading {
            @include reset-margins;
        }

        .panel-title {
            font-size: 2rem;
            letter-spacing: -.0315625em;

            a {
                position: relative;
                display: block;
                padding: 1em 1.8em;
                color: $black;
                background-color: $white;
                border-bottom: 1px solid #afafaf;
                transition: all 0.3s ease-in-out;

                &:hover, &:focus, &:active {
                    text-decoration: none;
                    background-color: #f7f7f7;
                }

                &:before {
                    @include icons-ionicons(20px,30px,auto,auto);
                    content: '\f3d1';
                }

                &:not(.collapsed) {
                    font-weight: 700;
                    background-color: #f7f7f7;
                    border-bottom: 1px solid transparent;

                    &:before {
                        transform: rotate(90deg);
                    }
                }
            }
        }

        .panel-body {
            position: relative;
            padding: 0 2em 2em;
            background-color: #f7f7f7;

            &:after {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 1px;
                background-color: $black;
                content: "";
            }

            h3 {
                padding: 0.5em 0 1em;
                font-size: 1.6rem;
                font-weight: 700;
                text-transform: uppercase;
                color: $black;
            }

            p {
                font-size: 90%;
                color: $gray-800;

                &:last-child {
                    margin-bottom: 0
                }
            }

            ul {
                margin: 0.5em 0 2em;
                font-size: 90%;
                color: $gray-800;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}






@mixin reset-margins {
    margin: 0;
    padding: 0;
}

@mixin reset-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin reset-flex {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
    align-content: space-between;
}

@mixin reset-flex-horizontal {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
}

@mixin reset-flex-stretch {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: stretch;
    align-content: stretch;
}

@mixin reset-button {
    padding: 0.8em 1.4em;
    font-size: 2rem;
    font-weight: 400;
    line-height: 1;
    letter-spacing: 0;
    // text-transform: uppercase;
    background: none;
    border: none;
    border-radius: 0;
    transition: all 0.3s ease-in-out;
}

@mixin button-style($color, $background-color, $border-color) {
    color: $color;
    background-color: $background-color;
    border: 2px solid $border-color;
}

@mixin button-hover-style($color, $background-color, $border-color) {
    color: $color;
    background-color: $background-color;
    border: 2px solid $border-color;
}

@mixin button-arrow {
    position: relative;
    padding-right: 6em !important;
    // font-weight: 600;
    text-align: left;
    text-transform: uppercase;

    // background-color: $complementary-color;
    // border-color: $complementary-color;

    // transition: all 0.2s;

    &:after {
        position: absolute;
        top: 10px;
        right: 20px;
        content: '\f3d3';
        font-family: 'Ionicons';
        font-size: 32px;
        font-weight: normal;
    }
}

@mixin button-hover {
    color: $white;
    background-color: $complementary-color;
    border-color: $complementary-color;
}


@mixin button-hover-shadow {
    color: $white !important;
    background-color: $complementary-color !important;
    border-color: $white !important;

    // width: 80%;
    // padding-right: 8em;

    box-shadow: -6px 6px 0 $main-color;
    // box-shadow: 0 0 8px $complementary-color;
}


@mixin button-hover-shadow-white {
    color: $white !important;
    background-color: $complementary-color !important;
    border-color: $complementary-color !important;

    // width: 80%;
    // padding-right: 8em;

    // box-shadow: -6px 6px 0 $white;

    box-shadow: -6px 6px 0 $white,
                0 19px 38px rgba(0, 0, 0, 0.30),
                0 15px 12px rgba(0, 0, 0, 0.22);


    // box-shadow: 0 0 8px $complementary-color;
}

@mixin text-link($color,$hover,$shadow-color,$shadow-hover) {
    color: $color;
    font-weight: 600;
    box-shadow: 0 1px 0 $shadow-color;
    transition: all 0.2s linear;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $complementary-color;
        box-shadow: 0 1px 0 $shadow-hover;
    }
}

@mixin link {
    color: $complementary-color;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $black;
    }
}

@mixin link-hover {
    text-decoration: none;
    color: $complementary-color;
    // box-shadow: 0 2px 0 $black;
}


@mixin link-line($color) {
    color: $color;
    box-shadow: 0 2px 0 transparent;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $color;
        box-shadow: 0 2px 0 $color;
    }
}

@mixin link-line-black {
    color: $black;
    box-shadow: 0 3px 0 transparent;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $black;
        background-color: transparent;
        box-shadow: 0 3px 0 $black;
    }
}

@mixin link-line-black-smaller {
    color: $black;
    box-shadow: 0 2px 0 transparent;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $black;
        background-color: transparent;
        box-shadow: 0 2px 0 $black;
    }
}

@mixin link-line-color {
    color: $complementary-color;
    box-shadow: 0 1px 0 transparent;

    &:hover,&:focus,&:active {
        text-decoration: none;
        color: $black;
        box-shadow: 0 1px 0 $black;
    }
}

@mixin link-bg-color {
    color: $black;

    &:hover,&:focus,&:active {
        text-decoration: none;
        // color: $black;
        background-color: rgba( $complementary-color, 0.3 );
        // background-color: lighten( $complementary-color, 10% );
        // background-color: desaturate( $complementary-color, 20% );
    }
}


// @mixin link-hover {
//     text-decoration: none;
//     color: $complementary-color;
//     box-shadow: 0 2px 0 $black;
// }


@mixin br-to-space {
    content:' ';

    &:after {
        content:' ';
    }
}



@mixin icons-ionicons($top,$right,$bottom,$left) {
    position: absolute;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
    font-family: 'Ionicons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    font-feature-settings: 'liga';
    transition: transform 0.3s ease-in-out;
}

@mixin reset-figure {
    position: relative;
    margin: 0 0;
    overflow: hidden;
}

@mixin stretch-to-left {
    @include reset-figure;
    margin-left: -22vw;

    img {
        width: 100%;
        max-width: 100%;
        height: auto;
    }
}

@mixin stretch-to-right {
    @include reset-figure;
    margin-right: -22vw;

    img {
        width: 100%;
        max-width: 100%;
        height: auto;
    }
}

@mixin marked-image {
    position: relative;

    &:after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 4px;
        height: 50px;
        background-color: $black;
    }
}

@mixin marked-box {
    position: relative;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 4px;
        height: 40px;
        background-color: $black;
    }
}

@mixin flex-boxes {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-content: stretch;
    align-items: stretch;
    margin: 0 -1em;
}

@mixin card-boxes {
    position: relative;
    width: calc(50% - 2em);
    margin: 1em;
    text-align: left;
    color: inherit;
    border-radius: 0;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    transition: 0.3s;

    &:hover,&:focus,&:active {
        box-shadow: 0 15px 30px rgba(0,0,0,.2);
        // box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
    }

    &:after {
        content: "";
        width: 100%;
        height: 100%;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        outline: transparent solid 1px;
        box-shadow: 0 0 0 rgba(0, 0, 0, 0.15);
        transition: 0.3s;
    }

    &:hover:after {
        box-shadow: 0 15px 30px rgba(0, 0, 0, 0.17)
    }
}

@mixin box-hover {
    color: $white;
    background-color: $complementary-color;
    transition: 0.2s ease-out;
}

@mixin box-hover-bw {
    color: $white;
    background-color: $black;
    transition: 0.2s ease-in-out;
}

@mixin box-hover-shadow {
    background-color: $light-black;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    transition: 0.3s;

    &:hover,&:focus,&:active {
        box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
    }
}

@mixin box-hover-outline {
    outline: 4px solid transparent;
    // outline-offset: 4px;
    transition: 0.3s;

    &:hover,&:focus,&:active {
        outline: 4px solid $complementary-color;
    }
}

@mixin highlight-text {
    margin-bottom: 0;
    font-size: 4.5rem;
    font-weight: 300;
    line-height: 1.2;
}

@mixin smaller-text {
    font-size: 90%;
    font-weight: 300;
    line-height: 1.2;
}

@mixin img-hover-fx {
    position: relative;
    overflow: hidden;
    transition: 0.3s;

    img {
        width: 100%;
        transform-origin: center center 0;
        transition: 0.6s;
    }

    &:hover img {
        transform: scale(1.1) translate(0);
    }
}


@mixin keyframes($name) {
    @keyframes #{$name} {
        @content
    }
}

@mixin animation($value) {
    animation: $value;
}

