            padding: 1.2em 0 0 1em;
            font-size: 1.6rem;
            font-weight: 700;
            line-height: 1.2;
            letter-spacing: -.06rem;
            text-transform: uppercase;
            color: $black;

            &:after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 4px;
                height: 40px;
                background-color: $black;
            }
        }

        .bookmark-button {
            @include reset-button;
            position: absolute;
            top: 10px;
            right: 0;
            width: 38px;
            height: 38px;
            margin: 0 0;
            padding: 0.2em;
            color: $dark-black;
            border-radius: 50%;
            z-index: 3;

            &:after {
                position: relative;
                top: 0;
                left: 0;
                content: '\e867';
                font-family: 'Material Icons';
                font-size: 24px;
                font-weight: normal;
                line-height: 1.2;
            }

            &.bookmark-on {
                &:after {
                    content: '\e866';
                }
            }

            &:hover, &:focus, &:active {
                @include button-hover;
            }
        }
    }

    .card-info {
        margin: 0 1em;
        padding: 0 0;

        .info-date {
            font-size: 84%;
            font-weight: 400;
            color: $soft-black;
        }

        .info-title {
            font-size: 90%;
            font-weight: 600;
            letter-spacing: 0;
            text-transform: uppercase;
            color: $medium-black;
        }
    }

    .card-body {
        @include reset-flex;
        align-items: stretch;
        justify-content: flex-start;

        margin: 1em 1em;
        padding: 0 0;

        h3 {
            margin-bottom: 0.5em;
            font-weight: 700;
        }

        p {
            margin-bottom: 1em;
            font-size: 90%;
            line-height: 1.2;
            color: $medium-black;
        }

