            text-transform: uppercase;
            color: $soft-black;

            &:after {
                @include icons-ionicons(-2px,auto,auto,0);
                content: '\f3a3';
            }
        }
    }

    .card-footer {
        margin: 0 0 auto;
        padding: 0 0;
        background-color: rgba(0,0,0,0);
        border-top: none;

        .view-more {
            min-width: auto;
