//
// SERVICES
// --------------------------------------------------------------------------

.insights-grid {
    margin: 6em auto 8em;
    // border: 1px solid red;
}

.grid-wrapper {
    display: -ms-grid;
    display: grid;
    grid-gap: 10px;

    // grid-gap IE11
    padding: 10px 0 0 10px;

    $col-width: (100% / 3);
    $row-height: 140px;

    -ms-grid-columns: $col-width $col-width $col-width;
    -ms-grid-rows: $row-height $row-height $row-height $row-height;

    grid-template-columns: $col-width $col-width $col-width;
    grid-template-rows: $row-height $row-height $row-height $row-height;

    justify-content: center;
    align-content: center;

    color: #444;
    background-color: #fff;

    .grid-box {
        font-size: 150%;
        padding: 20px;
        color: #333;
        background-color: #fcf;

        // grid-gap IE11
        border: 0 solid $white;
        border-width: 0 10px 10px 0;

        &.a {
            -ms-grid-column: 1;
            -ms-grid-column-span: 2;
            -ms-grid-row: 1;
            -ms-grid-row-span: 2;

            grid-column: 1 / span 2;
            grid-row: 1 / span 2;
        }
        &.b {
            -ms-grid-column: 3;
            -ms-grid-row: 1;
            -ms-grid-row-span: 2;

            grid-column: 3;
            grid-row: 1 / span 2;
        }
        &.c {
            -ms-grid-column: 1;
            -ms-grid-row: 3;
            -ms-grid-row-span: 2;

            grid-column: 1;
            grid-row: 3 / span 2;
        }
        &.d {
            -ms-grid-column: 2;
            -ms-grid-row: 3;

            grid-column: 2;
            grid-row: 3;
        }
        &.e {
            -ms-grid-column: 3;
            -ms-grid-row: 3;

            grid-column: 3;
            grid-row: 3;
        }
        &.f {
            -ms-grid-column: 2;
            -ms-grid-row: 4;

            grid-column: 2;
            grid-row: 4;
        }
        &.g {
            -ms-grid-column: 3;
            -ms-grid-row: 4;

            grid-column: 3;
            grid-row: 4;
        }
    }
}




