        .filter-lawyer {
            .row {
                padding: 0 2em;
            }
        }

        h2, h5 {
            color: $soft-black;
            letter-spacing: 0.02rem;
        }

        h2 {
            margin-bottom: 0.6em;
            font-size: 3rem;
        }

        h5 {
            margin-bottom: 1em;
            font-size: 1.8rem;
        }
    }

    .search-results-wrapper {
        text-align: center;

        .pagination {
            margin-bottom: 4em;

            .page-link {
                position: relative;
                display: block;
                width: 34px;
                height: 34px;
                padding: 0 0;
                margin: 0 1px;
                font-size: 2.4rem;
                font-weight: 600;
                line-height: 1.3;
                color: $soft-black;
                background-color: #fff;
                border: none;
                border-radius: 50%;

                &:hover,&:focus,&:active,.active {
                    @include button-hover;
                }
            }

            .page-item:first-child .page-link {
                margin-left: 0;
                border-top-left-radius: 50%;
                border-bottom-left-radius: 50%;
            }

            .page-item:last-child .page-link {
                margin-right: 0;
                border-top-right-radius: 50%;
                border-bottom-right-radius: 50%;
            }
        }

        .search-results-info {
            margin-bottom: 3em;

            .search-results-text {
                margin-top: 0.6em;
                color: $medium-black;
            }
        }

        .search-results-boxes {
            @include flex-boxes;

            .card {
                @include card-boxes;
                @include img-hover-fx;

                width: calc(25% - 2em);

                figure {
                    @include reset-figure;
                    min-height: 180px;

                    img {
                        width: auto;
                        height: 180px;
                    }
                }

                .card-title {
                    margin-bottom: 0.75rem;
                }

                .card-body {
                    display: flex;
                    flex-direction: column;
                    position: relative;
                    padding: 2rem;
                    color: $black;
                    background-color: $light-black;

                    .lawyer-name {
                        padding-bottom: 0.5em;
                    }

                    .lawyer-position {
                        margin-top: auto;
                        line-height: 1;
                    }

                    .lawyer-location {
                        line-height: 1;
                    }

                    &:after {
                        @include icons-ionicons(auto,10px,5px,auto);
                        content: '\f3d1';
                        font-size: 20px;
                        color: $half-black;
                    }
                }

                &:hover,&:focus,&:active {
                    .card-body {
                        @include box-hover;
                        // outline: 2px solid $black;

                        &:after {
                            color: $white;
                        }
                    }
                }
            }
        }

        .view-more {
            margin: 6rem 0 3rem;
        }
    }
}



